import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../scss/Nav.scss';

export class Nav extends Component {
    documentStyle = document.documentElement.style;

    handleScroll = () => {
        if (window.scrollY > 30) {
            this.documentStyle.setProperty('--nav-bg', 'rgb(29, 27, 30)');
            this.documentStyle.setProperty('--nav-shadow', '0px 4px 7px rgb(78, 77, 77)');
        } else {
            this.documentStyle.setProperty('--nav-bg', 'none');
            this.documentStyle.setProperty('--nav-shadow', 'none');
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    render() {
        return (
            <nav>
                <ul>
                    <li><Link className="nav-links" to="/">HOME</Link></li>
                    <li><Link className="nav-links" to="/about">ABOUT</Link></li>
                    <li><Link className="nav-links" to="/contactus">CONTACT US</Link></li>
                </ul>
            </nav>
        )
    }
}

export default Nav
