import React, { Component } from 'react'
import Map from './Map.js'
import BG1 from '../../images/bg1.jpg'
import '../../scss/Home.scss'

export class Home extends Component {
    render() {
        return (
            <div>
                <section className="home_section">
                    <div>
                        <img src={ BG1 } alt="bg1" />
                        <div className="headline">
                            <div>CAPTURE</div>
                            <div>EVERY</div>
                            <div className="flip">
                                <div><div>EVENT</div></div>
                                <div><div>MOMENT</div></div>
                                <div><div>ADVENTURE</div></div>
                            </div>
                        </div>
                    </div>
                </section>

                <section className="about_section">
                    <div className="container">
                        <h6>about</h6>
                        <div className="custom_heading-container">
                            <h2>PHOTOGRAPHY</h2>
                        </div>
                        <p>
                            "Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                            Nunc et lectus sed neque laoreet mattis. Vestibulum sagittis facilisis nisl, vel venenatis leo consectetur fermentum. 
                            Etiam scelerisque erat quis porta lacinia. Mauris laoreet tristique porta. 
                            Vivamus id dui eu mauris porta ultricies quis eu augue. 
                            Duis luctus, metus non lacinia rhoncus, nulla enim pellentesque est, vitae varius felis nisi iaculis sem. 
                            Morbi varius auctor justo, sit amet accumsan elit. 
                            Etiam sagittis tristique enim. Donec sapien ex, laoreet nec orci sed, convallis molestie nisi. 
                            Sed aliquet quam et tincidunt semper. Sed justo ligula, rutrum eu suscipit lobortis, finibus quis turpis. 
                            Maecenas at aliquam lacus, nec pharetra quam. 
                            Vestibulum sit amet dui at eros sollicitudin porta. 
                            Fusce tincidunt massa eu libero accumsan, sit amet varius quam euismod. 
                            Pellentesque lobortis, orci id lobortis viverra, arcu ex tincidunt ante, in mollis enim nibh et libero. 
                            Vestibulum justo massa, placerat at egestas id, tempor nec nisl."
                        </p>
                        <div>
                            <a href="none">Read More</a>
                        </div>
                    </div>
                </section>

                <section className="contact_section">
                    <div className="map-box">
                        <Map/>
                    </div>
                    <div className="contact_header">
                        <h2>CONTACT US</h2>
                    </div>
                    <div className="form-location">
                        <div className="contact-form">
                            <form>
                                <div class="contact_form-container">
                                    <input type="text" placeholder="Name" />
                                    <input type="email" placeholder="Email" />
                                    <input type="text" placeholder="Phone Number" />
                                    <input type="text" placeholder="Message" className="message_input" />
                                    <div class="mt-5">
                                        <button type="submit">
                                            send
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div className="contact_items">
                            <a href="none">
                                <div className="item ">
                                    <div className="img-box box-1"></div>
                                    <div className="detail-box">
                                    <p>
                                        Lorem ipsum dolor sit amet, consectetur adipiscing elit
                                    </p>
                                    </div>
                                </div>
                            </a>
                            <a href="none">
                                <div className="item ">
                                    <div className="img-box box-2"></div>
                                    <div className="detail-box">
                                    <p>
                                        +01 23456789
                                    </p>
                                    </div>
                                </div>
                            </a>
                            <a href="none">
                                <div className="item ">
                                    <div className="img-box box-3"></div>
                                    <div className="detail-box">
                                    <p>
                                        sample@sample.com
                                    </p>
                                    </div>
                                </div>
                            </a>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default Home
